import TaskMenu from "@/components/Todo/TaskMenu"
import { format } from 'date-fns'
export default {
  components:{
    TaskMenu
  },
 methods: {
    doneTask(id) {
      this.$store.commit("doneTask", id);
    }
  },
  filters:{
    niceDate(value){
      return format(new Date(value), 'd MMM yy')
    }
  },
  props:[
    'task'
  ]
}
