import draggable from "vuedraggable"

import task from "@/components/Todo/Task"

export default {
  components: {
    task,
    draggable
  },
  computed: {
    tasks: {
        get() {
            return this.$store.getters.tasksFiltered
        },
        set(value) {
            this.$store.commit('setTasks', value)
        }
    }
}
  
  
 
};
