export default {
  data() {
    return {
      newTaskTitle: "",
    };
  },

  methods: {
    addTask() {
      if (this.newTaskTitle) {
        this.$store.dispatch("addTask", this.newTaskTitle);
        this.newTaskTitle = "";
      }
    },
  },
  computed: {
    newTaskTitleInvalid() {
      return !this.newTaskTitle;
    },
  },
};
