import DialogDelete from "@/components/Todo/Dialogs/DialogDelete";
import DialogEdit from "@/components/Todo/Dialogs/DialogEdit";
import DialogDuedate from "@/components/Todo/Dialogs/DialogDueDate";
export default {
  data: () => ({
    items: [
      {
        title: "Edit",
        icon: "mdi-pencil",
        click() {
          this.dialogs.edit = true;
        },
      },
      { title: "Due Date", icon: "mdi-calendar",
        click() {
          this.dialogs.duedate = true;
        }, },
      {
        title: "Delete",
        icon: "mdi-delete",
        click() {
          this.dialogs.delete = true;
        },
      },
      {
        title: "Sort",
        
        icon: "mdi-drag-horizontal-variant",
        click() {
          this.$store.commit('toggleSorting')
        },
      },
    ],
    dialogs: {
      delete: false,
      edit: false,
      duedate:false
    },
  }),
  components: {
    DialogDelete,
    DialogEdit,
    DialogDuedate
  },
  methods: {
    handleClick(index) {
      this.items[index].click.call(this);
    },
  },
  props: ["task"],
};
