export default {
     data: () => ({
      date:''
    }),
    methods:{
        saveDate(){
            let payload={
                id:this.task.id,
                dueDate:this.date
            }
            this.$store.dispatch("updateDueDate",payload)
            this.$emit('close')
        }
    },
    props:[
        'task'
    ],
    mounted(){
        if(this.task.dueDate){
            this.date=this.task.dueDate
        } else {
            this.date=new Date().toISOString().substr(0, 10)
        }
        
    }
};
